*{
    padding: 0%;
    margin: 0%;
  }
  ul li{
    list-style: none;
  }
  .u1 li{
    float: left;
    font-family: '宋体';
    font-size: 20px;
    padding-left: 10px;
    padding-top: 15px;
  }
  .u1 li:first-child{
    color: #3fcccb;
  }

  .u2 li{
    width: 60px;
    height: 50px;
    display: inline;
    background-color: red;
    /* margin-left: 50px; */
    margin: 0 auto;
    border-radius: 90px;
    margin-top:30px;
    overflow: hidden;

  }
  
  .u3 li{
    display: inline;
    margin: 0 auto;
    font-size: 18px;
    overflow: hidden;
  }
  .u4 li{
    display: inline;
    margin: 0 auto;
    margin-top: 15px;
    overflow: hidden;
  }

.u5 li{
  display: inline;
  margin: 0 auto;
  margin-top: 10px;
  overflow: hidden;
}